import Dropdown from "./dropdown"
import { useGlobalData } from "../hooks/useGlobalData"

export default function SearchComponent({ displaySettings, setDisplaySettings }) {
    const { filters, setFilters, } = useGlobalData();

    const CAMPUS = ['' ,'', '', '', '', 'ecourse ']
    const DAY = ['', '', '', '', '', '', '', '']
    const CLASS_TIME = ['', '1', '2', '3', '4', 'F', '5', '6', '7', '8', '9', 'A', 'B', 'C', 'D']
    const GRADE = ['', '1', '2', '3', '4', '5']
    const COURSE_TYPE = [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
    ]

    const DEPARTMENTS = [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
    ]
    const EDUCATION_LEVEL = ['', '', '', '', '']

    function onCampusSelected(value) {
        setFilters({ ...filters, campus: value })
    }

    function onDepartmentSelected(value) {
        setFilters({ ...filters, department: value })
    }

    function onEducationLevelSelected(value) {
        setFilters({ ...filters, educationLevel: value })
    }

    function onGradeSelected(value) {
        setFilters({ ...filters, grade: value })
    }

    function onCourseTypeSelected(value) {
        setFilters({ ...filters, courseType: value })
    }

    function onDaySelected(value) {
        setFilters({ ...filters, day: value })
    }

    function onStartClassSelected(value) {
        setFilters({ ...filters, startClass: value })
    }

    function onEndClassSelected(value) {
        setFilters({ ...filters, endClass: value })
    }

    function onKeywordChanged(value) {
        setDisplaySettings({ ...displaySettings, keyword: value })
    }

    function onShowConflictedCheckChange(value) {
        setDisplaySettings({ ...displaySettings, isShowedConflictedCourses: value })
    }


    return (
        <div>

            <div className="mb-1">
                <input className="search-bar rounded-pill border-0 shadow-sm mt-3 w-75 ml-3" value={displaySettings.keyword} onChange={e => onKeywordChanged(e.target.value)} placeholder=""></input>
                <details>

                    <summary>
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-funnel-fill" viewBox="0 0 16 16">
                            <path d="M1.5 1.5A.5.5 0 0 1 2 1h12a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-.128.334L10 8.692V13.5a.5.5 0 0 1-.342.474l-3 1A.5.5 0 0 1 6 14.5V8.692L1.628 3.834A.5.5 0 0 1 1.5 3.5v-2z" />
                        </svg>
                        ()
                    </summary>
                    <div className="row">
                        
                        <div className="btn-group btn-group-sm flex-wrap" role="group">
                            <Dropdown key="campus" dropdownName="" defaultValue={filters.campus} dropdownItems={CAMPUS} onSelected={onCampusSelected} />
                            <Dropdown key="education_level" dropdownName="" defaultValue={filters.educationLevel} dropdownItems={EDUCATION_LEVEL} onSelected={onEducationLevelSelected}/>
                            <Dropdown key="grade" dropdownName="" defaultValue={filters.grade} dropdownItems={GRADE} onSelected={onGradeSelected} />
                            <Dropdown key="course_type" dropdownName="" defaultValue={filters.courseType} dropdownItems={COURSE_TYPE} onSelected={onCourseTypeSelected} />
                        </div>
                        <div className="btn-group btn-group-sm flex-wrap">
                            <Dropdown key="department" dropdownName="" defaultValue={filters.department} dropdownItems={DEPARTMENTS} onSelected={onDepartmentSelected} />
                            <Dropdown key="day" dropdownName="" defaultValue={filters.day} dropdownItems={DAY} onSelected={onDaySelected} />
                            <Dropdown key="start_class" dropdownName="" defaultValue={filters.startClass} dropdownItems={CLASS_TIME} onSelected={onStartClassSelected} />
                            <Dropdown key="end_class" dropdownName="" defaultValue={filters.endClass} dropdownItems={CLASS_TIME} onSelected={onEndClassSelected} />

                        </div>
                        <div className="btn-group btn-group-sm  form-inline flex-wrap">
                            <div className="form-check form-switch offset-md-9 ">
                                <label className="form-check-label fs-5" htmlFor="flexSwitchCheckChecked"></label>
                                <input className="form-check-input" type="checkbox" id="flexSwitchCheckChecked" checked={!displaySettings.isShowedConflictedCourses} onChange={() => onShowConflictedCheckChange(!displaySettings.isShowedConflictedCourses)} />
                            </div>
                        </div>
                    </div>

                </details>
            </div>
        </div>
    )

}